// @ts-check
import '@fontsource/roboto/300.css';
import '@fontsource/roboto/400.css';
import '@fontsource/roboto/500.css';
import '@fontsource/roboto/700.css';

import './App.css';
import Map from "./components/Map";
import {useEffect, useState} from "react";
import Stats from "./components/Stats";
import Container from '@mui/material/Container';
import React from 'react';
import Buttons from "./components/Buttons";
import {Card, Slider} from "@mui/material";
import Jobs from "./components/Jobs";

function App() {
    const colors = ['Aqua', 'DeepPink', 'Indigo', 'Yellow', 'Black', 'DarkCyan'];
    const [jobsData, setJobsData] = useState([]);
    const [distJobsData, setDistJobsData] = useState([])
    const [filteredJobs, setFilteredJobs] = useState([])
    const [color, setColor] = useState('Indigo');
    const [value, setValue] = React.useState(250);
    const [maxDist, setDist] = React.useState(250)

    const handleSliderChange = (event, newValue) => {
        setValue(newValue);
        setDist(newValue);

        const filteredJobs = jobsData.filter((job) => {
            console.log(job.distance, value)
            if(Number(job.distance) && Number(job.distance) < value) {
                return job
            }
            if(!job.distance && (job.Metro === 'Padova' || job.Metro === 'Bologna')) {
                return job
            }
        });
        setFilteredJobs(filteredJobs)
        setDistJobsData(filteredJobs)

    };
    const handleDateFilter = (data, date, index) => {
        const filtered = data.filter((job) => job.DueDate === date);
        // setFilteredJobs(filtered);
        setColor(colors[index])

        const filteredJobs = filtered.filter((job) => {
            console.log(job.distance, value)
            if(Number(job.distance) && Number(job.distance) < value) {
                return job
            }
            if(!job.distance && (job.Metro === 'Padova' || job.Metro === 'Bologna')) {
                return job
            }
        });
        setFilteredJobs(filteredJobs)



    };

    useEffect(() => {
        fetch('https://api.bitbucket.org/2.0/repositories/doctorrsm/ik-job-js-bot/src/master/data/jobs.json')
            .then(response => response.json())
            .then(data => {
                // Save the fetched data in the state
                const filteredJobs = data.filter((job) => {

                    if(job.distance && job.distance < value) {

                        return job
                    }
                    if(!job.distance && (job.Metro === 'Padova' || job.Metro === 'Bologna')) {
                        return job
                    }
                });

                setJobsData(data);
                setFilteredJobs(filteredJobs)
                setDistJobsData(filteredJobs)
            })
            .catch(error => {
                console.error('Error:', error);
            });
    }, []);


    return (
        <Container sx={{position:'relative'}}>
            <Slider
                value={typeof value === 'number' ? value : 0}
                onChange={handleSliderChange}
                aria-labelledby="input-slider"
                defaultValue={30}
                step={1}
                min={0} max={250}
            /> {value}
            <Jobs jobsData={filteredJobs} />
            <Buttons jobsData={distJobsData} handlClick={handleDateFilter}/>
            {!jobsData && <p>Подождите, пока загрузятся данные</p>}
            {/*<Stats jobs={jobsData}/>*/}
            {jobsData && <Map jobsData={filteredJobs} markerColor={color}/>}
        </Container>);
}

export default App;
