import React from 'react';
import { DataGrid } from '@mui/x-data-grid';

function Jobs(props) {
    const columns = [
        { field: 'Address', headerName: 'Адрес', width: 150 },
        { field: 'DueDate', headerName: 'Дата', width: 150 },
        { field: 'Scope', headerName: 'Работа', minWidth: 150, flex: 1, },
        { field: 'Duration', headerName: 'Часы', width: 150 },
        {field: 'distance', headerName: 'Расстояние', width: 150 },
        { field: 'City', headerName: 'Город', width: 150 }
    ];


    return (
        <div>
            <DataGrid
                rows={props.jobsData}
                columns={columns}
                getRowId={(row) =>  row.Id} getRowHeight={() => 'auto'}

            />
        </div>
    );
}

export default Jobs;