// @ts-check
import React, {useEffect, useRef, useState} from "react";
import L from "leaflet";
import "leaflet/dist/leaflet.css";
import Button from '@mui/material/Button';
import ButtonGroup from '@mui/material/ButtonGroup';

function Map(props) {

    const mapRef = useRef(null);
    const [currentJobs, setCurrentJobs] = useState(props.jobsData);


    useEffect(() => {
        setCurrentJobs(props.jobsData)


        // create map
        var map = L.map("map", {
            center: [45.41, 11.866667], zoom: 6,
        });

        var tiles = new L.tileLayer("https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png", {
            attribution: '&copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors',
            minZoom: 5,
        }).addTo(map);

        // Iterate over the jobs array to create markers on the map
        props.jobsData.forEach((job) => {
            if (job.latitude && job.longitude) {
                const googleMapsUrl = `https://www.google.com/maps?q=${job.latitude},${job.longitude}`;
                var formattedStartTime = new Date(job.StartTime).toLocaleString();


                var markerIcon = L.icon({
                    iconUrl: `/img/${props.markerColor}/${job.Duration}.png`, iconSize: [64, 32], // Adjust the size of the icon if needed
                });

                var marker = L.marker([job.latitude, job.longitude], {
                    draggable: true, icon: markerIcon,
                });

                marker.addTo(map).bindPopup(`
          <b>${formattedStartTime}</b> 
          <br><span style="display: block;font-family: monospace; padding: 6px 0; color: #3333FF; font-size: 16px; line-height: 1.1">${job.Scope.toString()}</span><br> 
          <b>Доп. плата ${job.Incentive} </b> <br> 
          <a href="https://taskrabbitlimited.outsystemsenterprise.com/TaskRabbitIKEAJobs/ClaimPage?Sheet1Id=${job.id}">Карточка заказа</a> <br>  <br>
           <a href="${googleMapsUrl}">Карта гугл</a>`, {
                    permanent: true, direction: "center",
                });


            }
        });

        return () => {
            map.remove();
        };

    }, [props.jobsData]);


    return (<div id="map" ref={mapRef} style={{height: "100vh", position: 'relative'}}>

        </div>);
}

export default Map;