import ButtonGroup from "@mui/material/ButtonGroup";
import Button from "@mui/material/Button";
import React, {useState} from "react";
import {Box, Typography} from "@mui/material";
// @ts-check
function Buttons (props) {
    const colors = ['Aqua', 'DeepPink', 'Indigo', 'Yellow', 'Black', 'DarkCyan'];


    const uniqueDueDates = props.jobsData.reduce((uniqueDates, job) => {
        if (!uniqueDates.includes(job.DueDate)) {
            uniqueDates.push(job.DueDate);
        }


        return uniqueDates;
    }, []);
    // const index = uniqueDueDates.findIndex(element => element === job.DueDate);
    uniqueDueDates.sort(function(a, b){
        var dateA = new Date(a), dateB = new Date(b);
        return dateA - dateB;
    });


    const [filteredJobs, setFilteredJobs] = useState([props.jobsData]);


    return (
        <Box sx={{ dispaly: 'flex', flexWrap: 'wrap', bottom: 30, left: 30, right: 0, zIndex: 1000 }} variant="contained" aria-label="outlined primary button group">
            {uniqueDueDates.map((date, index) => (
                <Button
                    key={index}
                    variant="outlined"
                    onClick={() => props.handlClick(props.jobsData, date, 3)}
                >
                    <Typography >{date}</Typography> - <Typography> {props.jobsData.filter((job) => job.DueDate === date).length}</Typography>;<br/>
                </Button>
            ))}
        </Box>
    )
}

export default Buttons;